<template lang="pug">
  div#contact.container
    div#markdown.page__wrapper
      div.contact__title
        h1.contact__title--text {{ $t('generic.title') }}

      div.contact__info
        p.contact__info--text(
          v-for="(value, key) in contactInfo"
          :key="key"
        ) {{ value.name }}:&nbsp;
          a.contact__info--link(
            target="_blank"
            :href="value.href"
            v-tooltip.right="{content: value.name + '( )', delay: {show: 500, hide: 100}}"
          ) {{ value.text }}

        p.contact__info--text {{ mail.name }}:&nbsp;
          span.contact__info--link(
            @click="toast"
            v-clipboard:copy="mail.text"
            v-tooltip.bottom="{content: ' ', delay: {show: 500, hide: 100}}"
          ) {{ mail.text }}
            i.fa.fa-clipboard

      GoogleMaps

      //- MailForm
</template>

<script>
import {globalVar} from '@/globalVar'
import GoogleMaps from '@/components/GoogleMaps'
import MailForm from '@/components/MailForm'

export default {
  name: 'contact',

  metaInfo: {
    title: globalVar.nameKo,
    titleTemplate: '%s - Contact',
  },

  data () {
    return {
      nameKo: globalVar.nameKo,
      contactInfo: {
        kakao: {
          name: ' ',
          href: globalVar.kakaoURL,
          text: '@',
        },
        fb: {
          name: '',
          href: globalVar.fbURL,
          text: ' ',
        },
      },
      mail: {
        name: '',
        text: globalVar.mailTo,
      },
    }
  },

  methods: {
    toast () {
      this.$toasted.show('  ', {
        theme: "primary",
        position: "bottom-center",
        duration : 2000
      })
    }
  },

  components: {
    GoogleMaps,
    MailForm
  },
}
</script>

<style lang="scss">

#contact {
  width: 100%;

  // .contact__title {

  //   .contact__title--text {

  //   }
  // }

  .contact__info {

    .contact__info--text {
      display: inline;
      @include line-height($grid5x);

      &:not(:last-child) {

        ::after {
          white-space: pre;
          content: '\a';
        }
      }

      .contact__info--link {
        cursor: pointer;

        &:hover {
          text-decoration: underline;
        }

        .fa-clipboard {
          color: $text999;
          padding-left: $grid;
        }
      }
    }
  }
}
</style>



